'use client';
import React from 'react';
import { useQuery } from '@tanstack/react-query';
import { getClient } from '@/services/clients';
import { Typography } from '@mui/material';

export default function ClientViewPage({ params }: { params: { id: string } }) {
  const q = useQuery({ queryKey: ['client', params.id], queryFn: () => getClient(params.id) });
  if (!q.data) return null;
  return (
    <>
      <Typography variant="h4" gutterBottom>{q.data.firstName} {q.data.lastName}</Typography>
      <Typography variant="body2">{q.data.email} â€” {q.data.sport}</Typography>
    </>
  );
}


